/* ------------------- TOPBAR MAIN ------------------- */
.topbar {
  background-color: #cc5908;
  color: white;
  font-size: 14px;
  height: 48px; /* keep fixed */
  padding-left: clamp(20px, 8vw, 150px); /* will shrink automatically */
  padding-right: clamp(20px, 8vw, 150px);
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 0 1px white;
  overflow: hidden;
}

/* LEFT SIDE */
.left-side {
  display: flex;
  align-items: center;
  gap: 20px;
  flex-shrink: 1; /* allow shrinking */
}

/* CONTACT ITEMS */
.contact-item {
  display: flex;
  align-items: center;
  gap: 6px;
  white-space: nowrap; /* keep in one line */
  flex-shrink: 1; /* allow shrinking */
}

.contact-text.phone {
  white-space: nowrap;
  line-height: 1.1;
  flex-shrink: 1; /* allow shrinking */
}

/* CONTACT ICONS */
.contact-icon {
  font-size: 16px;
  flex-shrink: 0;
}

/* RIGHT SIDE */
.right-side {
  display: flex;
  align-items: center;
  gap: 10px;
  flex-shrink: 0; /* social icons don't shrink */
}

/* SOCIAL ITEMS */
.social-item {
  display: flex;
  align-items: center;
  position: relative;
  flex-shrink: 0;
}

/* SOCIAL ICONS */
.social-icons {
  color: white;
  font-size: 20px;
  cursor: pointer;
  padding: 2px;
  border-radius: 4px;
  flex-shrink: 0;
  transition: background 0.3s, transform 0.2s;
}

/* HOVER EFFECT */
.social-icons:hover {
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(2px);
  transform: scale(1.1);
}

/* SEPARATOR */
.social-item:not(:last-child)::after {
  content: "";
  position: absolute;
  right: -8px;
  top: 50%;
  transform: translateY(-50%);
  width: 1px;
  height: 30px;
  background-color: white;
}

/* ------------------- MOBILE SHRINKING ------------------- */
@media (max-width: 768px) {
  .topbar {
    padding-left: 10px;  /* push content more to left */
    padding-right: 10px; /* reduce right spacing */
  }

  .left-side {
    gap: 12px; /* reduce spacing between email and phone */
  }

  .contact-item {
    gap: 4px; /* shrink icon-text gap */
  }

  .social-item:not(:last-child)::after {
    display: none; /* remove separator to save space */
  }
}
